.typewriter-div {
  display: none; /* Initially hide all divs */
  white-space: nowrap;
  overflow: hidden;
  animation: typing 5s steps(200, end)  , blink-caret 0.75s step-end infinite;
}

.active {
  display: block; /* Show the active div */
}
.typewriter{
 margin-left: 40%;
 margin-top: 10%;
 color: white;
 font-size: 2rem;
 /* height:150px; */
 width:400px;

 /* border: 2px solid red; */
 overflow-y: scroll;
 animation: scrollup 140s;
}
@keyframes scrollup {
  from{
   margin-top: 40%;
  }
   to{
      margin-top: -35%;
   }
  
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
    /* visibility: none; */
    
  }
}

@keyframes blink-caret {
  0% {
    border-right: 2px solid transparent;
  }
  50% {
    border-right: 2px solid white;
   
  }

}


